.App{
    display: flex;
    justify-content: space-around; 
    align-items: center;  
    height: 70vh;
}

.controls{
    width: 30vw;
}

.box{
    width: 40vw;
    height: 50vh;
    background-color: blueviolet;
    color: #fff;
}

.center{
    text-align: center;
}

input[type="color"] {
	-webkit-appearance: none;
	border: none;
	width: 100%;
	height: 50px;
}
input[type="color"]::-webkit-color-swatch-wrapper {
	padding: 0;
}
input[type="color"]::-webkit-color-swatch {
	border: none;
}

.innerbox{
    height: 20vh;
    width: 80%;
    padding: 20px 0 0 20px;
    background: #444;
    margin: 100px 0 0 80px;
}